.file-upload {
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
    -webkit-justify-content: center;
    -ms-flex-pack: center;
    justify-content: center;
    -webkit-box-align: center;
    -webkit-align-items: center;
    -ms-flex-align: center;
    align-items: center;
    position: relative;
    cursor: pointer;
    overflow: hidden;
    width: 100%;
    max-width: 100%;
    padding: 40px 10px;
    text-align: center;
    color:#CACACA;
    background:#F7F7F7 !important;
}

.file-upload , .btn-danger{
     border-radius: 10px;
    -webkit-border-radius: 10px; 
    -moz-border-radius: 10px; 
    
}

