.sidenav {
    
    height: 100%;
    width: 0;
    position: fixed;
    z-index: 1;
    top: 0;
    left: 0;
    background-color:#fff;
    overflow-x: hidden;
    transition:  0.01s;
box-shadow: #00000029 0.5px 0px 0px 0px;
    
